a {
    text-decoration: none;
    display: inline;
    color: black;
}
a:hover {
  color: white;
}
header {
    width: 100vw;
    height: 140px;
    display: grid;
    grid-template-rows: 1fr 1fr;
}
header .header-icons {
    /* width: 100%; */
    height: 50px;
    display: grid;
    /* background-image: linear-gradient(90deg, #B46C00, #717606, #337337, #006857, #005964, #2F4858); */
    background-color: rgb(0, 0, 0, 0.5);
}
header .header-icons .icons {
    width: 300px;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    justify-self: end;
    margin-right: 30px;
}
nav {
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 90px;
    background-color: rgb(0, 0, 0, 0.7);
    color: #eee;
}
nav .about-me a {
    color: #B46C00;
    border-bottom: 1px solid #B46C00;
}
nav .nav-logo-container {
    /* width: 400px; */
    height: auto;
    display: flex;
    margin-left: 30px;
    align-items: center;
}
.nav-logo-container img {
    width: 60px;
}
nav .about-me {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: 30px;
}

@media (max-width: 600px) {
    nav .about-me a {
        width: 80px;
    }
}
.search-bar {
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: rgba(255,255,255,0.2);
    border: none;
    box-shadow: 3px 2px 8px 0px rgba(0,0,0,0.5);
    border-radius: 10px;
    padding: 20px 0;
}
.search-bar .search {
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: rgba(0,0,0,0.1);   */
}
.search-bar .search form {
    display: flex;
    flex-direction: column;
    max-width: 37vw;
    justify-content: center;
}
.search label {
    background-color: rgba(255,255,255,0.3);
    font-size: 1.4rem;
    padding: 5px 15px;
    text-align: center;
    border-radius: 10px;
    border: none;
}
.search input {
    margin: 5px 0 0;
    height: 40px;
    border: none;
    border-radius: 10px;
    padding: 15px;
    text-align: center;
    font-size: 1.8rem;
}

.search form button {
    width: 48px;
    height: 48px;
    margin: 5px auto;
    border-radius: 24px;
    background-color: black;
    color: white;
    font-size: 3rem;
    cursor: pointer;
}
.search-bar .search-text {
    display: flex;
    justify-self: center;
    display: flex;
    align-items: center;
    width: 80%;
}
.search-bar .search-text p {
    background-color: rgba(0,0,0,0.2);
    padding: 5px 15px 5px;
    align-items: center;
    text-align: center;
    border-radius: 15px;
    color: #ccc;
    border: none;
}
@media (max-width: 600px) {
    .search-bar {
        display: grid;
        grid-template-rows: 1.5fr 1fr;
        grid-template-columns: 1fr;
        justify-items: center;
    }
    .search {
        grid-row: 1;
        width: 85%;
    }
    .search form {
        min-width: 60vw;
    }
    .search-text {
        grid-row: 2;
    }
}
