.poke-info {
    display: grid;
    height: auto;
    grid-template-columns: 1fr 1fr;
    padding: 15px;
    background-color: rgba(255,255,255,0.2);
    border: none;
    box-shadow: 3px 2px 8px 0px rgba(0,0,0,0.5);
    border-radius: 10px;
    margin-top: 20px;
}
.poke-info .poke-view {
    display: flex;
    flex-direction: column;
    justify-self: center;
    justify-items: center;
    align-items: center;
}
.name-image h2 {
    text-align: center;
}
.poke-info .poke-about {
    display: flex;
    justify-self: center;
    align-items: center;
    width: 70%;
}
.poke-info img {
    width: 300px;
    background-color: rgb(255, 255, 255, 0.25);
    border: 7px solid rgb(0, 0, 0, 0.2);
    background-clip: padding-box;
    border-radius: 20px;
}
.poke-info .types {
    margin-left: 45px;
}

@media (max-width: 600px) {
    .poke-info {
        display: grid;
        grid-template-rows: auto auto;
        grid-template-columns: 1fr;
        justify-items: center;
    }
    .poke-info .poke-view {
        display: flex;
        flex-direction: column;
        justify-items: center;
    }
    .poke-info .types {
        margin: 0px auto 10px;
    }
    .poke-info .poke-about {
        width: 80%;
        margin: 20px 0;
    }
}
.evolution {
    padding: 15px 30px 0;
}
.evolution h2 {
    text-align: center;
    color: #ddd;
    text-shadow: 2px 2px 5px rgba(0,0,0,0.7);
}
.evolution-number {
    padding: 10px 0;
    display: grid;
    justify-items: center;
    
}
.opTwo {
    grid-template-columns: 1fr 1fr;
}
.opThree {
    grid-template-columns: 1fr 1fr 1fr;
}
.evolution-number img {
    height: 120px;
    width: 120px;
}
.evolution-number div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 180px;
    background-color: rgba(255,255,255,0.2);
    border: none;
    box-shadow: 3px 2px 8px 0px rgba(0,0,0,0.5);
    border-radius: 10px;
}
.evolution-number h3, p {
    display: flex;
    justify-content: center;
}
@media (max-width: 600px) {
    .opTwo {
        grid-template-columns: 1fr;
    }
    .opThree {
        grid-template-columns: 1fr;
    }
    .evolution-number div {
        margin: 10px 0;
    }
}
.footer {
    width: 100%;
    height: 80px;
    background-color: rgb(0, 0, 0, 0.8);
    border-top: 20px solid rgb(0, 0, 0, 0.4);
    background-clip: padding-box;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #717606;
}
.ModalLoader {
    background-color: rgb(54, 54, 54);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-animation: loader 1.5s linear forwards;
            animation: loader 1.5s linear forwards;
};

@-webkit-keyframes loader {
    0% {
        opacity: 1;
        visibility: visible;
    }
    95% {
        opacity: 1;
        visibility: visible;
    }
    100% {
        opacity: 0;
        visibility: hidden;
    }
}

@keyframes loader {
    0% {
        opacity: 1;
        visibility: visible;
    }
    95% {
        opacity: 1;
        visibility: visible;
    }
    100% {
        opacity: 0;
        visibility: hidden;
    }
}

.ModalLoader div {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin: 10px;
    /* animation: scaling 1.5s ease-in-out infinite;
    transform: scale(0); */
}
.ModalLoader div:nth-child(1) {
    background-color: black;
    -webkit-animation-delay: 0.2s ;
            animation-delay: 0.2s ;
    -webkit-animation: scalingA 1s ease-in-out infinite;
            animation: scalingA 1s ease-in-out infinite;
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
}
.ModalLoader div:nth-child(2) {
    background-color: rgb(255, 153, 0);
    -webkit-animation-delay: 1s ;
            animation-delay: 1s ;
    -webkit-animation: scalingB 1s ease-in-out infinite;
            animation: scalingB 1s ease-in-out infinite;
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
}
.ModalLoader div:nth-child(3) {
    background-color: rgb(180, 108, 0);
    -webkit-animation-delay: 0.6s ;
            animation-delay: 0.6s ;
    -webkit-animation: scalingC 1s ease-in-out infinite;
            animation: scalingC 1s ease-in-out infinite;
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
}

@-webkit-keyframes scalingA {
    0%, 100% {
        background-color: black;
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
    }
    40% {
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: rgb(255, 153, 0);
    }
    50% {
        background-color: rgb(180, 108, 0);
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes scalingA {
    0%, 100% {
        background-color: black;
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
    }
    40% {
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: rgb(255, 153, 0);
    }
    50% {
        background-color: rgb(180, 108, 0);
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
@-webkit-keyframes scalingB {
    20% {
        background-color: rgb(255, 153, 0);
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
    }
    60% {
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: rgb(180, 108, 0);
    }
    70% {
        background-color: black;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
@keyframes scalingB {
    20% {
        background-color: rgb(255, 153, 0);
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
    }
    60% {
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: rgb(180, 108, 0);
    }
    70% {
        background-color: black;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
@-webkit-keyframes scalingC {
    40% {
        background-color: black;
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
    }
    80% {
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: rgb(255, 153, 0);
    }
    90% {
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: rgb(180, 108, 0);
    }
}
@keyframes scalingC {
    40% {
        background-color: black;
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
    }
    80% {
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: rgb(255, 153, 0);
    }
    90% {
        -webkit-transform: scale(1);
                transform: scale(1);
        background-color: rgb(180, 108, 0);
    }
}
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    width: auto;
}
html {
    font-size: 62.5%;
}
body {
    font-size: 1.6rem;
    font-family: "Roboto Mono", monospace;
    height: 100%;
    width: 100%;
    background-image: linear-gradient(135deg, #B46C00, #717606, #337337, #006857, #005964, #2F4858);
    background-size: 500%;
    -webkit-animation: animated 10s infinite;
            animation: animated 10s infinite;
}
main {
    padding: 40px 30px 20px;
    min-height: calc(100vh - 220px);
    width: 100%;
}
main > section {
    max-width: 1000px;
    margin: 0 auto;
}
@-webkit-keyframes animated {
    0%{
      background-position: 0% 50%;
    }
    50%{
      background-position: 100% 50%;
    }
    100%{
      background-position: 0% 50%;
    }
  }
@keyframes animated {
    0%{
      background-position: 0% 50%;
    }
    50%{
      background-position: 100% 50%;
    }
    100%{
      background-position: 0% 50%;
    }
  }
